body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;

  background-color: #008eed;
  background-image: url("https://www.transparenttextures.com/patterns/my-little-plaid.png");

  /* background-color: rgb(237, 95, 97); */
  /* background-color: rgb(255, 93, 102); */
  /* background-image: url("https://www.transparenttextures.com/patterns/fake-luxury.png"); */
  /* background-image: url("https://www.transparenttextures.com/patterns/60-lines.png");  */
}

.newPostButton {
  height: 60px;
  width: 80px;
  border: 4px solid white;
  border-radius: 20px;

  font-size: 38px;
  font-weight: bolder;
  margin: 30px 0 0 50px;
  position: fixed;
  
  color: white;
  background-color: rgb(255, 199, 65);
  -webkit-box-shadow: 3px 3px 3px rgb(80, 80, 80);
          box-shadow: 3px 3px 3px rgb(80, 80, 80)
}

.newPostButton:hover {
  -webkit-transform: scale(1.12);
      -ms-transform: scale(1.12);
          transform: scale(1.12);
  cursor: pointer;
}

.newPostButton:focus {
  outline: 0;
}



